<template>
  <div class="order-wrap">
    <div class="order-list-choose">
      <div class="order-list-option">
        选择产品：
        <v-selection :selections="products" @on-change="productchange">
        </v-selection>
      </div>
      <div class="order-list-option">
        开始日期：
        <datepicker @on-change="changeStartdate"> </datepicker>
      </div>
      <div class="order-list-option">
        结束日期：
        <datepicker @on-change="changeEnddate"> </datepicker>
      </div>
      <div class="order-list-option">
        关键词
        <input type="text" v-model.lazy="query" class="order-query" />
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th
            v-for="head in tableHeads"
            @click="changeOrder(head)"
            :class="{ active: head.active }"
          >
            {{ head.label }}
          </th>
        </tr>
        <tr v-for="item in tableDate">
          <td v-for="head in tableHeads">
            <!--@click="tdint(this)"-->
            {{ item[head.key] }}
          </td>
        </tr>
      </table>
      <div></div>
    </div>
  </div>
</template>

<script>
import VSelection from "components/base/selection";
import datepicker from "components/base/datepicker";
import _ from "lodash";
// import { set } from 'vue'
export default {
  components: {
    VSelection,
    datepicker,
  },
  data() {
    return {
      query: "",
      productId: 0,
      starDate: "",
      endDate: "",
      // tableDate:[],
      currentOrder: "desc",
      total: 0,
      pageSize: 10,
      offser: 0,
      products: [
        {
          label: "数据统计",
          value: 0,
        },
        {
          label: "数据预测",
          value: 1,
        },
        {
          label: "流量分析",
          value: 2,
        },
        {
          label: "广告发布",
          value: 3,
        },
      ],
      tableHeads: [
        {
          label: "订单号",
          key: "orderId",
        },
        {
          label: "购买产品",
          key: "product",
        },
        {
          label: "版本类型",
          key: "version",
        },
        {
          label: "有效时间",
          key: "period",
        },
        {
          label: "购买日期",
          key: "date",
        },
        {
          label: "数量",
          key: "buyNum",
        },
        {
          label: "总价",
          key: "amount",
        },
      ],
    };
  },
  watch: {
    query() {
      this.$store.dispatch("fetchOrderList");
    },
  },
  computed: {
    // tableDate(){
    //     return this.$store.getters.getOrderList
    // }
    tableDate: {
      get() {
        return this.$store.getters.getOrderList;
      },
      set(v) {
        this.$store.commit('changePrderList',v)
      },
    },
  },
  methods: {
    productchange(obj) {
      this.$store.commit("updateParams", {
        key: "productId",
        val: obj.value,
      });
      this.$store.dispatch("fetchOrderList");
    },
    changeStartdate(data) {
      this.$store.commit("updateParams", {
        key: "starDate",
        val: data,
      });
    },
    changeEnddate(data) {
      this.$store.commit("updateParams", {
        key: "endDate",
        val: data,
      });
    },
    getTableDate() {
      let reqParams = {
        query: this.query,
        productId: this.productId,
        endDate: this.endDate,
        starDate: this.starDate,
      };
      this.$http
        .get("http://localhost:3000/getOrderList", reqParams)
        .then((res) => {
          this.tableDate = res.data.list;
          console.log(res);
        });
    },
    changeOrder(headItem) {
      this.tableHeads.map((item) => {
        item.active = false;
        return item;
      });
      console.log(headItem);
      headItem.active = true;
      if (this.currentOrder === "desc") {
        this.currentOrder = "asc";
      } else if (this.currentOrder === "asc") {
        this.currentOrder = "desc";
      }
      console.log(this.tableDate);
      this.tableDate = _.orderBy(
        this.tableDate,
        headItem.key,
        this.currentOrder
      );
    },
  },
  mounted() {
    this.$store.dispatch("fetchOrderList");
  },
};
</script>

